import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    loggedIn: false
  },
  getters: {
    loggedIn: state => state.loggedIn
  },
  mutations: {
    setLoggedIn(state, loggedIn){
      state.loggedIn = loggedIn;
    }
  },
  actions: {
    login() {
      this.commit('setLoggedIn', true);
    },
    logout() {
      this.commit('setLoggedIn', false);
    }
  }
})
